import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import Basket from "./basket";
import { deleteGameInBasket} from "../../redux/basketReduser";



class BasketContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <Basket  baskets={this.props.baskets} gameItems={this.props.gameItems} deleteGameInBasket={this.props.deleteGameInBasket}/>
        )
    }



}

let mapStateToProps = (state) => {
    return {
        baskets: state.baskets.basketData,
        gameItems: state.gameList.gameData
    }
}


export default compose(
    connect(mapStateToProps , {
        deleteGameInBasket
    })
)(BasketContainer)

