import React, {useState} from 'react';
import style from './Admin.module.css'





function AdminPanelElem(props) {


    const deleteGameLokal = () => {
        props.deleteGameAdmin(props.gameItems.id,props.gameItems.delete)
    }
    const changeGame = () => {
        props.changeGameAdmin(props.gameItems)
    }
    let noveltyClick = () => {
        props.noveltyChange(props.gameItems.novelty,props.gameItems.id)
    }
    let popularClick = () => {
        props.popularChange(props.gameItems.popular,props.gameItems.id)
    }



    return (

        <div className={style.element}>
            <div className={style.name}>
                <h3>{props.gameItems.nameGame}</h3>
                <span>{props.gameItems.price} р</span>
            </div>
            <div className={style.hot}>
                <span > новинка: <r onDoubleClick={noveltyClick}>{(props.gameItems.novelty) ? 'true' : 'false'}</r></span>
                <span> популярное: <r onDoubleClick={popularClick}>{(props.gameItems.popular) ? 'true' : 'false'}</r></span>
            </div>
            <div className={style.add}>
                {props.gameItems.delete ?
                    <a onClick={deleteGameLokal} className={style.add_vernyt} href="##">вернуть </a>
                :
                    <a onClick={deleteGameLokal} className={style.add_delete} href="##">удалить </a>
                }
                <a onClick={changeGame} className={style.add_ezm} href="##">изменить</a>
            </div>
        </div>

    );


}

export default AdminPanelElem;