import React, {useState} from 'react';
import style from './basket.module.css';

import BasketItem from "./basketItem/basketItem";
import {NavLink} from "react-router-dom";


function Basket(props) {

    let check = false;
    let basketElem = props.baskets.map( (e) => {
        let needEl = props.gameItems.filter( item => {
            if(item.id == e.id){
                return {item}
            }
        })
        if(needEl[0] != null){
            return <BasketItem poster={needEl[0].poster_1_1} name={needEl[0].nameGame} price={needEl[0].price} deleteGameInBasket={props.deleteGameInBasket} id={needEl[0].id}/>
        }
    })

    if(props.baskets.length > 0){
        check = true;
    }else {
        check = false;
    }





    return (
        <div className={style.basket}>
            <div className={style.basket_container}>
                <div className={style.main}>
                        {basketElem}
                        <div className={style.buy_con}>
                            {check ? <NavLink className={style.buy} to={"/Thank"}>Купить</NavLink> : <p>у вас нет покупок</p>}
                        </div>
                </div>
            </div>
        </div>
    );
}

export default Basket;