import * as firebase from "firebase";

const ADD_GAME_IN_BASKET = 'ADD_GAME_IN_BASKET';
const DELETE_GAME_IN_BASKET = 'DELETE_GAME_IN_BASKET';
const CLEAR_BASKET = 'CLEAR_BASKET';



let initialState = {
    basketData: [
        
    ]
    /*followingInProgress: [1,2]*/
}



const basketReducer = (state = initialState, action) =>  {


    switch (action.type) {
        case  ADD_GAME_IN_BASKET:
            let id = action.id;
            let check = true;
            for(let i = 0; i < state.basketData.length ; i++){
                if(state.basketData[i].id == id){
                    check = false
                }
            }
            if(check){
                return {
                    ...state,
                    basketData: [...state.basketData ,  {id: id}]
                }
            }
        case DELETE_GAME_IN_BASKET:
            let id2 = action.id;
            for(let i = 0; i < state.basketData.length; i++){
                if(id2 == state.basketData[i].id){
                    state.basketData.splice(i , 1 )
                }
            }

            return {
                ...state,
                basketData: [ ...state.basketData ]
            }
        case CLEAR_BASKET:
            return {
                ...state,
                basketData: []
            }
        default:
            return state;
    };
};



