
import style from './../main.module.css';
import React from "react";
import Slider from "react-slick";
import next from './../../../file/images/next.svg'
import back from './../../../file/images/back.svg'

import car_1 from './../../../file/images/frostpunk.jpg'
import car_2 from './../../../file/images/Hearts-of-Iron.jpg'
import car_3 from './../../../file/images/civilization6.jpg'

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div  style={{ ...style, display: "block",height: "50px",position: "absolute",top: "50%",right: "0",marginTop: "-25px", cursor: "pointer",zIndex: "9" }} onClick={onClick}>
            <img src={next} alt="" style={{ ...style, display: "block",height: "50px" }}/>
        </div>
    );
}

function SamplePrevArrow(props) {
   const { className, style, onClick } = props;
    return (
        <div   style={{ ...style, display: "block",height: "50px",position: "absolute",top: "50%",left: "0",marginTop: "-25px", cursor: "pointer",zIndex: "9"}} onClick={onClick}>
            <img src={back} alt="" style={{ ...style, display: "block",height: "50px" }}/>
        </div>
    );
}



class SimpleSlider extends React.Component {
    render() {
        const settings = {
            className: "center",
            centerMode: true,
            infinite: true,
            centerPadding: "0",
            slidesToShow: 1,
            speed: 500,
            autoplay: true,
            autoplaySpeed: 5000,
            pauseOnHover: true,
            nextArrow: <SampleNextArrow />,
            prevArrow: <SamplePrevArrow />
        };
        return (
            <div>
                <Slider {...settings}>
                    <div className={style.carousel_item}>
                        <img src={car_1} alt=""/>
                    </div>
                    <div className={style.carousel_item}>
                        <img src={car_2} alt=""/>
                    </div>
                    <div className={style.carousel_item}>
                        <img src={car_3} alt=""/>
                    </div>
                </Slider>
            </div>
        );
    }
}


export default SimpleSlider;