import React from 'react';
import style from './games.module.css';
import logo_item from './../../file/images/gtafans-ru-gta.jpg'
import GamesItem from "./gamesItem/gamesItem";

function Games(props) {

    let basketsCount = []
    for (let i = 0; i < props.baskets.length ; i++){
        basketsCount[i] = props.baskets[i].id
    }

    let listGame = props.gameItems.map( (e) =>{
        if(!e.delete) {
            return <GamesItem isAuth={props.isAuth} novelty={e.novelty} popular={e.popular} poster={e.poster_1_1}
                              addGameInBasket={props.addGameInBasket} name={e.nameGame} description={e.description}
                              price={e.price} id={e.id} basketsCount={basketsCount}/>
        }
    })


    return (
        <div className={style.Games}>
            <div className={style.Games_container}>
                <div className={style.Games_main}>
                    {listGame}
                </div>
            </div>
        </div>
    );
}

export default Games;