import React, {useEffect, useState} from 'react';
import style from './Admin.module.css'
import {Field, reduxForm} from "redux-form";


function AdminAddForm (props) {




    return (
        <form className={style.formAdd} onSubmit={props.handleSubmit}>
            <span>Название:</span>
            <Field  component={"input"} name={"name"}  className={style.formAdd_input} type="text"/>
            <span>Описание:</span>
            <Field  component={"textarea"}  rows={"3"} name={"description"}  className={style.formAdd_textArea} type="text"/>
            <span>Цена:</span>
            <Field  component={"input"} name={"price"}  className={style.formAdd_input} type="text"/>
            <div className={style.formAdd_box_con}>
                <span>Новинка:</span>
                <Field  component={"input"} name={"novelty"}  className={style.formAdd_box} type="checkbox"/>
            </div>
            <div className={style.formAdd_box_con}>
                <span>Популярное:</span>
                <Field  component={"input"} name={"popular"}   className={style.formAdd_box} type="checkbox"/>
            </div>
            <div className={style.formAdd_box_con + ' ' + style.formAdd_numberDiv}>
                <span>Оценка игры:</span>
                <Field  component={"input"} name={"number"} min={"1"} max={"5"} className={style.formAdd_number} type="number"/>
            </div>
            <div className={style.formAdd_box_con}>
                <span>Постер(1:1):</span>
                {/*<Field  component={"input"} onChange={onChange}  className={style.formAdd_photo} type="file"/>*/}
                <input type="file" onChange={props.onChangeFile1} name={"photo_1_1"}/>
            </div>
            <div className={style.formAdd_box_con}>
                <span>Постер(3:2):</span>
                {/*<Field  component={"input"} name={"photo3_2"}  className={style.formAdd_photo} type="file"/>*/}
                <input type="file" onChange={props.onChangeFile2} name={"photo3_2"}/>
            </div>
            <button /*onClick={props.handleSubmit}*/ className={style.formAdd_button}>Добавить</button>
        </form>
    );
}

const AdminAddReduxForm = reduxForm({
    form: 'addNewGame'
})(AdminAddForm)


function AdminAdd(props) {



    const [imageAsFile, setImageAsFile] = useState('')
    const [imageAsFile2, setImageAsFile2] = useState('')
    const [check, setCheck] = useState(false)
    const refProgress1 = React.createRef()
    const refProgress2 = React.createRef()


    const onChangeFile1 = (e) =>{
        const image = e.target.files[0];
        setImageAsFile((imageFile ) => image)
    }


 /*   const onChangeFile1 = (e) =>{
        const image = e.target.files[0];
        setImageAsFile(() => image)
        console.log(imageAsFile);
    }*/
    const onChangeFile2 = (e) =>{
        const image2 = e.target.files[0]
        setImageAsFile2((imageFile ) => image2)
    }


 /*   let file1
    let file2
    const onChangeFile1 = (e) =>{
        file1 = e.target.files[0];
    }
    const onChangeFile2 = (e) =>{
        file2 = e.target.files[1]
    }*/


    useEffect(() => {
        if((check) && (props.progress_poster_1 == 100) && (props.progress_poster_2 == 100)){
            setTimeout(
                successAdd
            ,2000)
        }
        refProgress1.current.style.width = props.progress_poster_1 + '%';
        refProgress2.current.style.width = props.progress_poster_2 + '%';
    })
    const successAdd = () => {
        props.successfullyAdd()
        props.progressPosterSuccess()
    }



    const  AddNewGame = (e) =>{
        let nameGame = e.name
        let description = e.description
        let price = e.price
        let novelty = e.novelty
        let popular = e.popular
        let appraisal = e.number
        let poster_1_1 = imageAsFile;
        let poster_3_2 = imageAsFile2;
        let id = props.count
        if((nameGame != '') && (description != '') && (price != '') && (appraisal != '')){
            props.sendNewGameDB(nameGame,description,price,novelty,popular,appraisal,id,poster_1_1,poster_3_2)
            setCheck(true)

        }
    }

    return (
        <div className={style.panel}>
            <span ref={refProgress1} className={style.progress_1}></span>
            <span ref={refProgress2} className={style.progress_2}></span>
            <div className={style.container}>
                <div className={style.mainAdd}>
                   <h1>Добавить</h1>
                     <AdminAddReduxForm onChangeFile2={onChangeFile2} onChangeFile1={onChangeFile1} onSubmit={AddNewGame}/>
                </div>
            </div>
        </div>
    );


}

export default AdminAdd;