const gameReducer = (state = initialState, action) =>  {
    switch (action.type) {
        case  SEND_GAME:
            let nameGame = action.nameGame;
            let description = action.description;
            let price = action.price;
            let id = state.gameData.length + 1;
            let novelty = action.novelty;
            let popular = action.popular;
            let appraisal = action.appraisal;
            let deleteEl = action.deleteEl;
            return {
                ...state,
                gameData: [...state.gameData ,  {nameGame: nameGame,description: description,price: price,id: id,novelty: novelty,popular: popular,appraisal: appraisal, delete: deleteEl}]
            }
        case SEND_POSTER_1:
            let poster_1_1 = action.poster_1_1;
            let id4 = action.id + 1;
            let newA = state.gameData
            let newData = newA.map( (e) => {
                if(e.id == id4) {
                    return {nameGame: e.nameGame,description: e.description,price: e.price,id: e.id,novelty: e.novelty,popular: e.popular,appraisal: e.appraisal, poster_1_1: poster_1_1, poster_3_2: e.poster_3_2, delete: e.delete }
                }else {
                    return e
                }
            })
            return {
                ...state,
                gameData: newData
            }
        case SEND_POSTER_2:
            let poster_3_2 = action.poster_3_2;
            let id5 = action.id +1;
            let newA2 = state.gameData
            let newData2 = newA2.map( (e) => {
                if(e.id == id5) {
                    return {nameGame: e.nameGame,description: e.description,price: e.price,id: e.id,novelty: e.novelty,popular: e.popular,appraisal: e.appraisal, poster_1_1: e.poster_1_1, poster_3_2: poster_3_2, delete: e.delete }
                }else {
                    return e
                }
            })
            return {
                ...state,
                gameData: newData2
            }
      /*  case DELETE_GAME:
            let idM = action.id
            let newObj = state.gameData.filter((e) => {
                return e.id !== idM;
            });
            let k = -1;
            let newobj2 = newObj.map((e) =>{
                k++
                return {nameGame: e.nameGame,description: e.description,price: e.price,id: k,novelty: e.novelty,popular: e.popular,appraisal: e.appraisal}
            })
            return {
                ...state,
                gameData: newobj2
            }*/
        case CLEAR_GAME_STATE:
            return {
                ...state,
                gameData: []
            }
        case SET_COUNT:
            return {
                ...state,
                count: action.k
            }
        case PROGRESS_POSTER_1:
            let progress = action.progress
            return {
                ...state,
                progress_poster_1: progress
            }
        case PROGRESS_POSTER_2:
            let progress2 = action.progress
            return {
                ...state,
                progress_poster_2: progress2
            }
        case PROGRESS_POSTER_SUCCESS:
            return {
                ...state,
                progress_poster_1: 0,
                progress_poster_2: 0
            }
        default:
            return state;
    };
};



export let getGameData = (nameGame,description,price,novelty,popular,appraisal,deleteEl) =>{
    return {
        type: SEND_GAME,
        nameGame,description,price,novelty,popular,appraisal,deleteEl
    }
}
export let getPosters_1_1 = (id,poster_1_1) =>{
    return {
        type: SEND_POSTER_1,
        id,poster_1_1
    }
}
export let getPosters_3_2 = (id,poster_3_2) =>{

    return {
        type: SEND_POSTER_2,
        id,poster_3_2
    }
}
export let clearGameState = () =>{
    return {
        type: CLEAR_GAME_STATE
    }
}

export let setCount = (k) =>{
    return {
        type: SET_COUNT,
        k
    }
}
export let progressPoster1 = (progress) =>{
    return {
        type: PROGRESS_POSTER_1,
        progress
    }
}
export let progressPoster2 = (progress) =>{
    return {
        type: PROGRESS_POSTER_2,
        progress
    }
}
export let progressPosterSuccess = () =>{
    return {
        type: PROGRESS_POSTER_SUCCESS
    }
}
export let deleteGameAdmin = (id,bool) =>{
    return async (dispatch) => {
        let idAk3 = id - 1
        const db2 = firebase.database();
        await db2.ref('Game/' + idAk3  + '/delete').set(!bool)
    }
}
export let popularChange = (bool,id) =>{
    return async (dispatch) => {
        let idAk1 = id - 1
        const db2 = firebase.database();
        if(bool){
            bool = bool
        }else {
            bool = false
        }
        await db2.ref('Game/' + idAk1  + '/popular').set(!bool)
    }
}
