export let sendNewGameDB = (nameGame,description,price,novelty,popular,appraisal,id,poster_1_1,poster_3_2) =>{
    return async (dispatch) => {
        const db = firebase.database();
        if(novelty){
            novelty = novelty
        }else {
            novelty = false
        }
        if(popular){
            popular = popular
        }else {
            popular = false
        }
        let meme = {
            nameGame: nameGame,
            description: description,
            price: price,
            novelty: novelty,
            popular: popular,
            appraisal: appraisal,
            id: id,
            delete: false
        }

        await db.ref('Game/' + id).set(meme)


        const uploadTask = firebase.storage().ref('images/'+ id +  '/poster_1_1' ).put(poster_1_1)
        const uploadTask2 = firebase.storage().ref('images/' + id +  '/poster_3_2').put(poster_3_2)


        uploadTask.on('state_changed' ,
            (snapshot) => {
            let progres_1 = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                dispatch(progressPoster1(progres_1))
            },
            (error) => {console.log(error)},
            () => {
                console.log('redy1')
        })
        uploadTask2.on('state_changed' ,
            (snapshot) => {
                let progres_2 = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                dispatch(progressPoster2(progres_2))
            },
            (error) => {console.log(error)},
            () => {

                console.log('redy2')
        })

    }
}


