import React from 'react';
import style from './Login.module.css'
import {Field, reduxForm} from "redux-form";
import * as firebase from "firebase";
import {Redirect} from "react-router-dom";

function LoginInForm (props) {

    return (
        <form className={style.login_main} onSubmit={props.handleSubmit}>
            <h1>Войти</h1>
            <span>Почта:</span>
            <Field  component={"input"} name={"login"}  className={style.log_input} type="text"/>
            <span>Пароль:</span>
            <Field  component={"input"} name={"password"}  className={style.log_input} type="password"/>
            <button /*onClick={props.handleSubmit}*/ className={style.log_button}>Отправить</button>
        </form>
    );
}

const LoginInReduxForm = reduxForm({
    form: 'loginIn'
})(LoginInForm)

function Login(props) {


    const loginIn = (value) =>{
        let login = value.login;
        let password = value.password;
        if((login != null) && (password != null)){
            addRegistr(value)
        }else {

        }
    }
    const addRegistr = (value) => {
        let login = value.login;
        let password = value.password;
        firebase.auth().signInWithEmailAndPassword(login,password)
            .then(
                error => {
                    console.log(error);
                    props.isLoginIn();
                }
            )

    }

    if(props.isAuth){
       return <Redirect to={"Games"} />
    }else{
        return (
            <div className={style.login}>
                <LoginInReduxForm onSubmit={loginIn} />
            </div>
        );
    }

}

export default Login;