import React from 'react';
import './App.css';
import {BrowserRouter, Route} from "react-router-dom";
import Futer from "./components/Futer/futer";
import Contacts from "./components/contacst/contacts";
import GamesContainer from "./components/Games/gamesContainer";
import GamesIdContainer from "./components/GameId/gamesIdContainer";
import AdminContainer from "./components/Login/AdminCon";
import RegistrContainer from "./components/Login/RegistrCon";
import LoginContainer from "./components/Login/LoginCon";
import BasketContainer from "./components/basket/basketContainer";
import HeaderContainer from "./components/Header/headerContainer";
import MainContainer from "./components/Main/mainContainer";
import {compose} from "redux";
import {connect} from "react-redux";
import * as firebase from "firebase";
import {isLoginIn} from "./redux/authReduser";
import ThankContainer from "./components/Thank/thankContainer";



class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {check: false};
    }


    componentDidMount() {
        firebase.auth().onAuthStateChanged(function(user) {
            if (user) {
                keke()
            } else {

            }

        });
        let keke = () => {
            this.setState({
                check: true
            })
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.state.check == true){
            this.props.isLoginIn()
        }
    }

    render() {
        return (
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <div className="App">
                    <div className="app_main">
                        <HeaderContainer/>
                        <div className="app-wrapper-content">
                            <Route exact path="/" render={() => <MainContainer/>}/>
                            <Route path="/GamesId/:gameId?" render={() => <GamesIdContainer/>}/>
                            <Route path="/Games" render={() => <GamesContainer/>}/>
                            <Route path="/Contacts" render={() => <Contacts/>}/>
                            <Route path="/Login" render={() => <LoginContainer/>}/>
                            <Route path="/Admin" render={() => <AdminContainer/>}/>
                            <Route path="/Registr" render={() => <RegistrContainer/>}/>
                            <Route path="/Basket" render={() => <BasketContainer/>}/>
                            <Route path="/Thank" render={() => <ThankContainer/>}/>
                        </div>
                        <Futer/>
                    </div>
                </div>
            </BrowserRouter>
        );
    }
}



let mapStateToProps = (state) => {
    return {

    }
};


export default compose(connect(mapStateToProps , {isLoginIn}))(App)
