import React from 'react';
import style from './../main.module.css';

import poster from "./../../../file/images/star-wars-battlefront-2.jpg"
import {NavLink} from "react-router-dom";

function MainElement(props) {

    const AddBasket = () =>{
        props.addGameInBasket(props.id)
    }

    let check = true;

    let a = props.basketsCount;
    for(let i = 0; i < a.length ; i++){
        if(a[i] == props.id){
            check = false
        }
    }

    return (

        <div className={style.Main_new_con_item}>
            <NavLink className={style.Main_navlink}  to={"/GamesId/" + props.id} >
                <img src={props.poster} alt=""/>
                <h1>{props.nameGame}</h1>
            </NavLink>
            <div className={style.Main_new_con_item_add}>
                <span>{props.price} р</span>
                {check ?
                    props.isAuth ?  <a className={style.Main_new_con_item_add_a} onClick={AddBasket} href="##">добавить</a>
                        :
                        <NavLink className={style.Main_new_con_item_add_a}  to={"/Login"}>добавить</NavLink>
                :
                    <a className={style.Main_new_con_item_remyv_a} onClick={AddBasket} href="##">убрать</a>
                }
            </div>
        </div>

    );
}

export default MainElement;