import React, {useState} from 'react';
import style from './Admin.module.css'
import AdminPanelElem from "./adminPanelElem";
import AdminChange from "./adminChange";




function AdminPanel(props) {

    let [change ,changeChange] = useState(true)
    let [change2 ,changeChange2] = useState({})


    let changeGameAdmin = (e) => {
        changeChange(false);
        changeChange2(e)
    }


    let AdminPenelArray = props.gameItems.map( (e) => {
        return <AdminPanelElem popularChange={props.popularChange} noveltyChange={props.noveltyChange}  deleteGameAdmin={props.deleteGameAdmin} changeGameAdmin={changeGameAdmin} gameItems={e}  />
    })

    if (change){
        return (
            <div className={style.panel}>
                <div className={style.container}>
                    <div className={style.main}>
                        {AdminPenelArray}

                        <div onClick={props.addNewGame} className={style.add_item}>
                            +
                        </div>
                    </div>
                </div>
            </div>
        );
    }else {
        return <AdminChange gameItems={change2} />
    }



}

export default AdminPanel;