import React from 'react';
import style from './../basket.module.css';
import logo_item from './../../../file/images/gtafans-ru-gta.jpg'
import {NavLink} from "react-router-dom";



function BasketItem(props) {

    const deleteGame = () =>{
        props.deleteGameInBasket(props.id)
    }


    return (
                    <div className={style.item}>
                        <NavLink to="/GamesId/1" className={style.col_1}>
                            <img src={props.poster} alt=""/>
                            <h1>{props.name}</h1>
                        </NavLink>
                        <div className={style.col_2}>
                            <h1>{props.price} р</h1>
                            <a onClick={deleteGame} href="###"> Убрать</a>
                        </div>
                    </div>
    );
}

export default BasketItem;