import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";

import GameId from "./gameId";
import {withRouter} from "react-router-dom";
import {addGameInBasket} from "../../redux/basketReduser";



class GamesIdContainer extends React.Component{
   componentDidMount() {


   }

    render(){
        return (
            <GameId isAuth={this.props.isAuth} gameElem={this.props.gameItem}  gameId={this.props.match.params.gameId} addGameInBasket={this.props.addGameInBasket} basketsCount={this.props.basketsCount}/>
        )
    }



}

let mapStateToProps = (state) => {
    return {
        gameItem: state.gameList.gameData,
        basketsCount: state.baskets.basketData,
        isAuth: state.form.isAuth
    }
}
let WithRouterGame = withRouter(GamesIdContainer)

export default compose(
    connect(mapStateToProps , {
        addGameInBasket
    })
)(WithRouterGame)

