import React from 'react';

import {compose} from "redux";
import {connect} from "react-redux";
import Admin from "./admin";
import {
    clearGameState,
    deleteGameAdmin,
    getGameData, noveltyChange, popularChange,
    progressPosterSuccess,
    sendNewGameDB,
    setCount
} from "../../redux/gameReduser";
import * as firebase from "firebase";



class AdminContainer extends React.Component {

    componentDidMount() {
        const db = firebase.database();
        let game = db.ref('Game')
        game.on('value', (elem) => {
            this.kek2(elem.val())
        })
    }

    kek2(e) {
        this.props.clearGameState()
        let id = 0
        for (let key in e) {
            this.props.getGameData(e[key].nameGame , e[key].description, e[key].price,e[key].novelty,e[key].popular,e[key].appraisal,e[key].delete)
            id++
        }
        this.props.setCount(id)
    }


    render() {
        return (
            <Admin count={this.props.count} sendNewGameDB={this.props.sendNewGameDB}
                   deleteGameAdmin={this.props.deleteGameAdmin} gameItems={this.props.gameItems}
                   getGameData={this.props.getGameData}  progress_poster_1={this.props.progress_poster_1}
                   progress_poster_2={this.props.progress_poster_2} progressPosterSuccess={this.props.progressPosterSuccess}
                   noveltyChange={this.props.noveltyChange} popularChange={this.props.popularChange}
            />
        )
    }
}



let mapStateToProps = (state) => {
    return {
        gameItems: state.gameList.gameData,
        count: state.gameList.count,
        progress_poster_1: state.gameList.progress_poster_1,
        progress_poster_2: state.gameList.progress_poster_2

    }
}


export default compose(
    connect(mapStateToProps , {
        getGameData,deleteGameAdmin,sendNewGameDB,setCount,clearGameState,progressPosterSuccess,popularChange,noveltyChange
    })
)(AdminContainer)