import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";

import Registr from "./Registracia";
import {isLoginIn} from "../../redux/authReduser";



class RegistrContainer extends React.Component {

    render() {
        return (
            <Registr isAuth={this.props.isAuth} isLoginIn={this.props.isLoginIn} />
        )
    }
}



let mapStateToProps = (state) => {
    return {
        isAuth: state.isAuthM.isAuth
    }
}


export default compose(
    connect(mapStateToProps , {
        isLoginIn
    })
)(RegistrContainer)