import React, {useState} from 'react';
import style from './Admin.module.css'
import {Field, reduxForm} from "redux-form";


function AdminChangeForm(props) {
    let [changeName, changeNameFun] = useState(true)
    let [changeDescription, changeDescriptionFun] = useState(true)

    return (
                <form className={style.main_change_form} onSubmit={props.handleSubmit}>
                    <div className={style.change_name}>
                        <h1>Название:</h1>
                        {changeName ?
                            <span onDoubleClick={() => changeNameFun(false)} >{props.gameItems.nameGame}</span>
                            :
                            <Field onBlur={() => changeNameFun(true)} autoFocus={true} defoultValue="1" component={"input"} name={"name"}  className={style.formAdd_input} type="text"/>
                        }
                    </div>
                    <div className={style.change_discripsion}>
                        <h1>Описание:</h1>
                        <span>{props.gameItems.description}</span>
                    </div>
                    <div className={style.change_new}>
                        <h1>Новинка:</h1>
                        <Field  component={"input"} name={"novelty"}   className={style.formAdd_box} type="checkbox"/>
                    </div>
                    <div className={style.change_pop}>
                        <h1>Популярное:</h1>
                        <Field  component={"input"} name={"popular"}    className={style.formAdd_box} type="checkbox"/>
                    </div>
                    <div className={style.change_ocenka}>
                        <h1>Оценка игры:</h1>
                        <Field  component={"input"} name={"number"} min={"1"} max={"5"} className={style.formAdd_number}  type="number"/>
                    </div>
                    <div className={style.formAdd_box_con}>
                        <h1>Постер:</h1>
                        {/*<Field  component={"input"} name={"photo"}  className={style.formAdd_photo} type="file"/>*/}
                    </div>
                    <button /*onClick={props.handleSubmit}*/ className={style.change_button}>Изменить</button>
                </form>

    );

}




const AdminChangeReduxForm = reduxForm({
    form: 'addNewGame'
})(AdminChangeForm)

function AdminChange(props) {



    return (
        <div className={style.change}>
            <div className={style.container}>
                <div className={style.main_change}>
                    <AdminChangeReduxForm gameItems={props.gameItems} />
                </div>
            </div>
        </div>
    );


}

export default AdminChange;