import React from 'react';
import style from './../games.module.css';
import logo_item from './../../../file/images/gtafans-ru-gta.jpg'
import {NavLink} from "react-router-dom";

function GamesItem(props) {

    const addGame = () => {
        props.addGameInBasket(props.id)
    }

    let check = true;

    let a = props.basketsCount;
    for(let i = 0; i < a.length ; i++){
        if(a[i] == props.id){
            check = false
        }
    }

    return (
            <div className={style.item}>
                <NavLink className={style.item_a} to={"/GamesId/" + props.id}>
                    <img src={props.poster} alt=""/>
                </NavLink>
                <div className={style.item_con}>
                    <NavLink className={style.item_a} to={"/GamesId/" + props.id}>
                        <h1>{props.name}</h1>
                    </NavLink>
                    <p>{props.description}</p>
                    <div className={style.item_fut}>
                        <span>{props.price} р</span>
                        {check ?
                            props.isAuth ? <a className={style.item_fut_add} onClick={addGame} href="##">добавить</a>
                                :
                                <NavLink className={style.item_fut_add} to={"/Login"}>добавить</NavLink>
                            :
                            <a className={style.item_fut_remuve} onClick={addGame} href="##">убрать</a>
                        }

                    </div>
                </div>
                <div className={style.card}>
                    {props.popular && <div className={style.card_p}>хит</div>}
                    {props.novelty && <div className={style.card_n}>новинка</div>}
                </div>
            </div>

    );
}

export default GamesItem;