import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import * as firebase from 'firebase'
import {addGameInBasket } from "../../redux/basketReduser";
import Main from "./main";
import {
    clearGameState,
    getGameData,
    getPosters_1_1,
    getPosters_3_2,
    setCount
} from "../../redux/gameReduser";



class MainContainer extends React.Component{



    componentDidMount() {
        const db = firebase.database();
        let game = db.ref('Game')
        game.on('value', (elem) => {
            this.kek(elem.val())
        })
    }

    kek(e) {
        this.props.clearGameState();
        let id = 0;
        let id2 = 0;
        let id3 = [];


        /* let id3 = 0
         for (let key in e) {
             this.posterF_1(id2);
             this.posterF_2(id2);
             id2++;
         }*/

        for (let key in e) {
            this.props.getGameData(e[key].nameGame , e[key].description, e[key].price,e[key].novelty,e[key].popular,e[key].appraisal,e[key].delete)
            id++
        }
        this.props.setCount(id)

        for(let i = 0; i < id; i++){
            id3[i] = i
        }

        const posterF_1 = (k) => {
            firebase.storage().ref('images').child(k + '/poster_1_1').getDownloadURL().then( url => {
                    this.props.getPosters_1_1(k,url);
                }
            )
        }

        const posterF_2 = (k) => {
            firebase.storage().ref('images').child(k + '/poster_3_2').getDownloadURL().then( url => {
                    this.props.getPosters_3_2(k,url);
                }
            )
        }


        async function posledArray(id3) {
            for (const e of id3){
                await posterF_1(e)
                await posterF_2(e)
            }
        }
        posledArray(id3)

        /*console.log(id2 , id )
        while (id2 < id) {
            firebase.storage().ref('images').child(id2 + '/poster_3_2').getDownloadURL().then( url => {
                    this.props.getPosters_3_2(id2,url);
                    id2++;
                    console.log(id2)
                }
            )
        }*/
    }



    /*posterF_1 = async (id2) =>{
        await firebase.storage().ref('images').child(id2 + '/poster_1_1').getDownloadURL().then( url => {
                this.props.getPosters_1_1(id2,url);
            }
        )
    }*/

   /* posterF_2 = async (id3) =>{
        await firebase.storage().ref('images').child(id3 + '/poster_3_2').getDownloadURL().then( url => {
                this.props.getPosters_3_2(id3,url);
            }
        )
    }*/


    render(){
        return (
            <Main isAuth={this.props.isAuth} addGameInBasket={this.props.addGameInBasket} gameItems={this.props.gameItems} baskets={this.props.baskets}/>
        )
    }



}

let mapStateToProps = (state) => {
    return {
        baskets: state.baskets.basketData,
        gameItems: state.gameList.gameData,
        isAuth: state.isAuthM.isAuth
    }
}


export default compose(
    connect(mapStateToProps , {
        addGameInBasket,clearGameState,getGameData,setCount,getPosters_1_1,getPosters_3_2
    })
)(MainContainer)

