import React from 'react';
import style from './main.module.css';
import SimpleSlider from "./calousel/carouselMain";
import poster from "./../../file/images/star-wars-battlefront-2.jpg"
import MainElement from "./mainElement/mainElement";

function Main(props) {

    let k1 = 0;
    let basketsCount = []
    for (let i = 0; i < props.baskets.length ; i++){
        basketsCount[i] = props.baskets[i].id
    }



    let noveltyGame = props.gameItems.map( e => {
            if((e.novelty == true) && (k1 < 3)){
                k1++
                if(!e.delete){
                    return <MainElement isAuth={props.isAuth} poster={e.poster_3_2} nameGame={e.nameGame} price={e.price} addGameInBasket={props.addGameInBasket} id={e.id}  basketsCount={basketsCount} />
                }
            }
        }
    )

    let k2 = 0;
    let popularGame = props.gameItems.map( e => {
            if((e.popular == true) && (k2 < 3)){
                k2++
                if(!e.delete) {
                    return <MainElement isAuth={props.isAuth} poster={e.poster_3_2} nameGame={e.nameGame}
                                        price={e.price} addGameInBasket={props.addGameInBasket} id={e.id}
                                        basketsCount={basketsCount}/>
                }
            }
        }
    )



    return (
        <div className={style.Main}>
            <div className={style.Main_curosel}>
                <div className="container">
                    <div className={style.Main_curosel_con}>
                        <SimpleSlider />
                    </div>
                </div>
            </div>
            <div className={style.palka}>
                <span className={style.palka_span_1}></span>
                <h2>Нови<f>нки</f></h2>
                <span className={style.palka_span_2}></span>
            </div>
            <div className={style.Main_new}>
                <div className="container">
                    <div className={style.Main_new_con}>
                        {noveltyGame}
                    </div>
                </div>
            </div>
            <div className={style.palka2}>
                <span className={style.palka_span_3}></span>
                <h2>Попул<f>ярное</f></h2>
                <span className={style.palka_span_4}></span>
            </div>
            <div className={style.Main_new}>
                <div className="container">
                    <div className={style.Main_new_con}>
                        {popularGame}
                    </div>
                </div>
            </div>

        </div>
    );
}

export default Main;