import React from 'react';
import style from './Login.module.css'
import {Field, reduxForm} from "redux-form";
import * as firebase from "firebase";
import {Redirect} from "react-router-dom";


function RegistrForm (props) {

    return (
        <form className={style.login_main} onSubmit={props.handleSubmit}>
            <h1>Регистрация</h1>
            <span>Почта:</span>
            <Field  component={"input"} name={"login"}  className={style.log_input} type="text"/>
            <span>Пароль:</span>
            <Field  component={"input"} name={"password"}  className={style.log_input} type="password"/>
            <button /*onClick={props.handleSubmit}*/ className={style.log_button}>Отправить</button>
        </form>
    );
}

const RegistrReduxForm = reduxForm({
    form: 'reg'
})(RegistrForm)

function Registr(props) {

    const registrFun = (value) =>{
        let login = value.login;
        let password = value.password;
        if((login != null) && (password != null)){
            addRegistr(value)
        }
    }
    const addRegistr = (value) => {
        let login = value.login;
        let password = value.password;
        firebase.auth().createUserWithEmailAndPassword(login,password)
            .then(
                error => {
                    console.log(error);
                    props.isLoginIn();
                }
            )

    }

    if(props.isAuth){
        return <Redirect to={"Games"} />
    }else {
        return (
            <div className={style.login}>
                <RegistrReduxForm onSubmit={registrFun} />
            </div>
        );
    }

}

export default Registr;