import React, {useState} from 'react';
import style from './header.module.css';
import user from './../../file/images/user.svg';
import corzin from './../../file/images/shopping-cart.svg';
import {NavLink} from "react-router-dom";
import * as firebase from "firebase";

function Header(props) {

    const [count, setCount] = useState(true);

    let refAuth = React.createRef();

    const authClick = () =>{
        if(count){
            refAuth.current.style.display= "flex"
            setCount(false)
        }else{
            refAuth.current.style.display= "none"
            setCount(true)
        }
    }



    const authBlur = () =>{
        if(count){
            refAuth.current.style.display= "flex"
            setCount(false)
        }else{
            refAuth.current.style.display= "none"
            setCount(true)
        }
    }

    const outAuth = () => {
        firebase.auth().signOut().then(r => {
            console.log(r);
            props.isLoginOut()
        })

    }


    return (
        <div className={style.header}>
            <div className="container">
                <div className={style.header_main}>
                    <div tabindex="1" className={style.header_papka}>
                        <NavLink  to="/">
                            <div className={style.logo}>Strategy<b>GAME</b> </div>
                        </NavLink>
                        <div className={style.nav}>
                            <NavLink activeClassName={style.activeLink} to="/Games">Игры</NavLink>
                        </div>
                        <div className={style.nav}>
                            <NavLink activeClassName={style.activeLink} to="/Contacts">Контакты</NavLink>
                        </div>
                    </div>
                    <div className={style.header_corzina} >
                        <div className={style.user_menu} >
                            {props.isAuth && <div className={style.user_menu_check}></div>}
                            <img  className={style.auth} onClick={authClick}   src={user} alt=""/>
                            <div  className={style.user_menu_hidden} ref={refAuth} onMouseLeave={authBlur}>
                                {props.isAuth ?
                                    <a href="##"  onClick={outAuth}>Выйти</a>
                                :
                                    <NavLink to={"/Login"}>Войти</NavLink>
                                }
                                {!props.isAuth &&  <NavLink to={"/Registr"}>Регистрация</NavLink>}
                                <NavLink to={"/Admin"}>Администрация</NavLink>
                            </div>
                        </div>
                        {props.isAuth ?
                        <NavLink className={style.search_main} to={"/Basket"}>
                            <img className={style.search} src={corzin} alt=""/>
                                {(props.baskets.length != 0) &&
                                    <div className={style.search_con}>
                                        <span className={style.search_count}>
                                            {props.baskets.length}
                                        </span>
                                    </div>
                                }
                        </NavLink>
                        :
                            <NavLink className={style.search_main} to={"/Login"}>
                                <img className={style.search} src={corzin} alt=""/>
                                {(props.baskets.length != 0) &&
                                <div className={style.search_con}>
                                        <span className={style.search_count}>
                                            {props.baskets.length}
                                        </span>
                                </div>
                                }
                            </NavLink>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Header;