import React from 'react';
import style from './gamesID.module.css';
import logo from './../../file/images/gtafans-ru-gta.jpg'
import star_zal from './../../file/images/star.svg'
import star from './../../file/images/star_2.svg'
import {NavLink} from "react-router-dom";

function GameId(props) {


    let game =  props.gameElem.filter( e => {
        return e.id == props.gameId;
    })

    let cout = game[0].appraisal;
    let k = 0;
    let starEl = [1,2,3,4,5];
   /* for(let i = 0; i <= 5; i++){
        starEl[i] = <img src={star_zal} alt=""/>
    }*/
    let starEl2 = starEl.map( () => {
        k++;
        if(cout >= k){
            return <img src={star} alt=""/>
        }else {
            return <img src={star_zal} alt=""/>
        }
    })

    let check = true;

    let a = props.basketsCount;
    for(let i = 0; i < a.length ; i++){
        if(a[i].id == props.gameId){
            check = false
        }
    }

    const addBasket = () =>{
        props.addGameInBasket(props.gameId)
    }


    return (
        <div className={style.GamesId}>
            <div className={style.GamesId_container}>
                <div className={style.main}>
                    <div className={style.logoName}>
                        <img src={game[0].poster_1_1} alt=""/>
                        <div className={style.conMain}>
                            <div className={style.description}>
                                <h1>{game[0].nameGame}</h1>
                                <div className={style.star}>
                                    {starEl2}
                                </div>
                                <p>{game[0].description} </p>
                            </div>
                            <div className={style.prise}>
                                <span>{game[0].price} р</span>
                                {check ?
                                    props.isAuth ? <a onClick={addBasket} href="##">Добавить в корзину</a>
                                        :
                                    <NavLink  to={"/Login"}>добавить</NavLink>
                                :
                                    <a className={style.prise_a_del} onClick={addBasket} href="##">Убрать из корзины</a>
                                }

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default GameId;