/*import * as firebase from "firebase";*/

const IS_LOGIN_IN = 'IS_LOGIN_IN';
const IS_LOGIN_OUT = 'IS_LOGIN_OUT';



let initialState = {
    authData: [
        
    ],
    isAuth: false
    /*followingInProgress: [1,2]*/
}



const authReducer = (state = initialState, action) =>  {


    switch (action.type) {
        case  IS_LOGIN_IN:
            return {
                ...state,
                isAuth: true
            }
        case IS_LOGIN_OUT:
            return {
                ...state,
                isAuth: false
            }

        default:
            return state;
    };
};



export let isLoginIn = () =>{
    return {
        type: IS_LOGIN_IN
    }
}
export let isLoginOut = () =>{
    return {
        type: IS_LOGIN_OUT
    }
}




export  default authReducer;