import React, {useEffect, useState} from 'react';
import style from './Login.module.css'
import {Field, reduxForm} from "redux-form";
import AdminPanel from "./AdminPanel/adminPanel";
import AdminAdd from "./AdminPanel/adminAdd";
import AdminChange from "./AdminPanel/adminChange";
import {popularChange, progressPoster1, successPoster1} from "../../redux/gameReduser";


function AdminForm (props) {

    return (
        <form className={style.login_main} onSubmit={props.handleSubmit}>
            <h1>Админка</h1>
            <span>Почта:</span>
            <Field  component={"input"} name={"login"}  className={style.log_input} type="text"/>
            <span>Пароль:</span>
            <Field  component={"input"} name={"password"}  className={style.log_input} type="password"/>
            <button /*onClick={props.handleSubmit}*/ className={style.log_button}>Отправить</button>
        </form>
    );
}



const AdminReduxForm = reduxForm({
    form: 'admin'
})(AdminForm)

function Admin(props) {
    let [check,checkChange] = useState('Vhod')
    const adminVhod = (e) => {
        let login = e.login;
        let password = e.password;
        if((login == "admin")&&(password == "admin")){
            checkChange('Panel')
        }
    }

    let addNewGame = () => {
        checkChange('add')
    }
    let successfullyAdd = () =>{
        checkChange('Panel')
    }



    if(check == 'Vhod'){
        return (
            <div className={style.login}>
                <AdminReduxForm onSubmit={adminVhod} />
            </div>
        )
    }else if(check == 'Panel'){
        return <AdminPanel  deleteGameAdmin={props.deleteGameAdmin} gameItems={props.gameItems} addNewGame={addNewGame} popularChange={props.popularChange} noveltyChange={props.noveltyChange}/>
    }
    else if(check == 'add'){
        return <AdminAdd count={props.count} sendNewGameDB={props.sendNewGameDB}
                         getGameData={props.getGameData} successfullyAdd={successfullyAdd}
                         progress_poster_1={props.progress_poster_1} progress_poster_2={props.progress_poster_2}
                         progressPosterSuccess={props.progressPosterSuccess}
                />
    }


}

export default Admin;